<template>
  <el-main id="main">
    <router-view></router-view>
  </el-main>
</template>

<script>
import $ from "jquery"

export default {
  methods: {
    mainMinHeight () {
      var minHeight = $(window).height() - 120 + 'px'
      $('#main').css('min-height', minHeight)
    }
  },
  mounted () {
    this.mainMinHeight()
    window.onresize = this.mainMinHeight
  }
}
</script>

<style lang="scss">
#main {
  background-color: rgb(243, 241, 241);
  overflow: hidden;
}
</style>

